import './../../assets/css/callCustomer.css';
export default {
    name: 'Call Customer',
    data: function(){
        return{
            phoneNumberList: [],
            loading: false,
            errorMessage: "Unable to fetch Customer Phone Number",
            url: null,
            showError: false,
            callCenter:null
        }
    },
    created(){
      let that = this;
      ORACLE_SERVICE_CLOUD.extension_loader.load("Global Extension", "1").then(function (extensionProvider) {
        extensionProvider.registerWorkspaceExtension(function(workspaceRecord)
        {
          var recordType = workspaceRecord.getWorkspaceRecordType();
          
          if(recordType == "Contact" || recordType == "Incident"){
            workspaceRecord.getFieldValues(['Contact.PhOffice','Contact.PhHome','Contact.PhMobile','Contact.PhFax','Contact.PhAsst']).then(function(IFieldDetails)
            {
              that.phoneNumberList[0] = {type : "MM Landline Number", number : (IFieldDetails.getField('Contact.PhOffice').getLabel()) ? IFieldDetails.getField('Contact.PhOffice').getLabel() : ""};
              that.phoneNumberList[1] = {type : "Public Landline Number", number : (IFieldDetails.getField('Contact.PhHome').getLabel()) ? IFieldDetails.getField('Contact.PhHome').getLabel() : ""};
              that.phoneNumberList[2] = {type : "Public Mobile Number", number : (IFieldDetails.getField('Contact.PhMobile').getLabel()) ? IFieldDetails.getField('Contact.PhMobile').getLabel() : ""};
              that.phoneNumberList[3] = {type : "MM Mobile Number", number : (IFieldDetails.getField('Contact.PhFax').getLabel()) ? IFieldDetails.getField('Contact.PhFax').getLabel() : "" };
              that.phoneNumberList[4] = {type : "Public CY Mobile Number", number : (IFieldDetails.getField('Contact.PhAsst').getLabel()) ? IFieldDetails.getField('Contact.PhAsst').getLabel() : "" };
              
            });
          }
          
        });
        extensionProvider.getGlobalContext().then(function (globalContext) {
          globalContext.registerAction('OutgoingCallError', function (param) {
             
              if(param.errorCode == "CALL_FAILED"){
                that.showError = true;
                that.errorMessage = "Outgoing call failed, please try again or check phone number.";
                that.loading = false;
              }
            
          });
        });
      });

      

    },

    methods: {
        phoneNumberSelected: function (selectedPhoneNumber) {
          this.loading = true;
          var that = this;
          
          ORACLE_SERVICE_CLOUD.extension_loader.load("Global Extension", "1").then(function (extensionProvider) {
            extensionProvider.getGlobalContext().then(function (globalContext) {
              var param = { phone_number: "*"+that.callCenter+selectedPhoneNumber};
              globalContext.invokeAction('PhoneNumberSelected', param);
            });
          });
        },

    
    
      }
  }